.myWorks{
  position: relative;
  z-index: 1;
  padding: 50px 0;
  background-color: rgba(255, 255, 255, 0.9);
  overflow: hidden;
}

.container{
  max-width: 1140px;
  margin: 0 auto;
}

.title {
  position: relative;
  margin: 0 0 80px 0;
  font: 700 40px/1.2 'Josefin Sans', sans-serif;
  letter-spacing: 5px;
  font-weight: 300;
  color: #000000;
  text-transform: capitalize;
  text-align: center;

  &:before {
    position: absolute;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
    content: "";
    width: 60px;
    height: 2px;
    background-color: #4e93e6;
  }
}


